export const data = {"time":1685891734247,"blocks":[{"id":"7mjVJE1GtW","type":"paragraph","data":{"text":"<b></b><b></b><b>«Пушкинская карта»</b> — это специальная банковская карта для молодежи, которой можно расплачиваться только при покупке билетов на культурные мероприятия. С ее помощью молодые люди в возрасте от 14 до 22 лет могут приобретать билеты в театр."}},{"id":"lzZtBX3Aik","type":"paragraph","data":{"text":"Карту можно оформить в любом отделении Почта Банка. Зарегистрировать виртуальную карту можно на портале <u class=\"cdx-underline\">«Госуслуги» в личном кабинете</u>. Деньги на счёт таких карт будет перечислять государство. 1 января 2022 года каждому владельцу карты программы «Пушкинская карта» было начислено 5000 рублей. Карта действует на всей территории России."}},{"id":"tO_AyR90-t","type":"paragraph","data":{"text":"<b>Приглашаем всех держателей «Пушкинской карты» на спектакли театра!</b>"}}],"version":"2.27.0"};